<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <div slot="header">
        <el-alert
          title=""
          type="warning"
          :closable="false"
        ></el-alert>
      </div>
      <el-row>
        <el-col>
          <span>:</span>
          <!--  -->
          <el-cascader
            v-model="selectcatList"
            :options="catList"
            :props="cateProps"
            @change="handleChange"
          ></el-cascader>
        </el-col>
      </el-row>
      <!-- tab  -->
      <el-tabs v-model="activeName" @tab-click="handelTab">
        <!--  -->
        <el-tab-pane label="" name="many">
          <el-button
            type="primary"
            :disabled="isBtnDisable"
            @click="adddialogVisible = true"
            ></el-button
          >

          <!--  -->
          <el-table :data="manyTableData" border stripe>
            <!--  -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <el-tag
                  type="primary"
                  v-for="(item, i) in scope.row.attr_vals"
                  :key="i"
                  closable
                  @close="handleColse(i, scope.row)"
                  >{{ item }}</el-tag
                >
                <!--  -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                >
                </el-input>
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(scope.row)"
                  ></el-button
                >
              </template>
            </el-table-column>
            <el-table-column type="index"> </el-table-column>
            <el-table-column label="" prop="attr_name">
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  icon="el-icon-search"
                  @click="showeditDiolog(scope.row.attr_id)"
                  ></el-button
                >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  @click="removeP(scope.row.attr_id)"
                  ></el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <!--  -->
    <el-dialog
      :title="'' + titletext"
      :visible.sync="adddialogVisible"
      width="30%"
      @close="adddiologclosed"
    >
      <el-form
        :model="addForm"
        :rules="addFormrules"
        ref="addFormref"
        label-width="100px"
      >
        <el-form-item :label="titletext" prop="attr_name">
          <el-input v-model="addForm.attr_name"></el-input> </el-form-item
      ></el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="adddialogVisible = false"> </el-button>
        <el-button type="primary" @click="addParams"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      :title="'' + titletext"
      :visible.sync="editdialogVisible"
      width="30%"
      @close="editdiologclosed"
    >
      <el-form
        :model="editForm"
        :rules="editFormrules"
        ref="editFormref"
        label-width="100px"
      >
        <el-form-item :label="titletext" prop="attr_name">
          <el-input v-model="editForm.attr_name"></el-input> </el-form-item
      ></el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editdialogVisible = false"> </el-button>
        <el-button type="primary" @click="editParams"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    return {
      // 
      inputVisible: false,
      // 
      inputValue: '',
      // 
      catList: [],
      // 
      cateProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // 
      selectcatList: [],
      // 
      activeName: 'many',
      //  
      manyTableData: [],
      onlyTableData: [],
      // 
      adddialogVisible: false,
      // 
      addForm: {},
      addFormrules: {
        attr_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      editdialogVisible: false,
      editForm: {},
      editFormrules: {
        attr_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
    //  false
    isBtnDisable () {
      if (this.selectcatList.length !== 3) {
        return true
      }
      return false
    },
    // id
    cateId () {
      if (this.selectcatList.length === 3) {
        return this.selectcatList[2]
      }
      return null
    },
    // 
    titletext () {
      if (this.activeName === 'many') {
        return ''
      }
      return ''
    }
  },
  methods: {
    // 
    async getCateList () {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.catList = res.data
    },
    //  
    handleChange () {
      this.getParamsData()
    },
    // tab
    handelTab () {
      this.getParamsData()
    },
    // 
    async getParamsData () {
      if (this.selectcatList.length !== 3) {
        // 
        this.selectcatList = []
        this.manyTableData = []
        this.onlyTableData = []
        return this.$message.error('')
      }
      // id
      const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, {
        params: {
          sel: this.activeName
        }
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // 
      res.data.forEach(item => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        //  
        item.inputVisible = false
        item.inputValue = ''
      })
      // 
      if (this.activeName === 'many') {
        this.manyTableData = res.data
      } else {
        this.onlyTableData = res.data
      }
    },
    // 
    adddiologclosed () {
      this.$refs.addFormref.resetFields()
    },
    //  
    addParams () {
      this.$refs.addFormref.validate(async valid => {
        if (!valid) {
          return this.$message.error('')
        }
        const { data: res } = await this.$http.post(`categories/${this.cateId}/attributes`, {
          attr_name: this.addForm.attr_name,
          attr_sel: this.activeName
        })
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.adddialogVisible = false
        this.getParamsData()
        return this.$message.success('')
      })
    },
    // 
    async showeditDiolog (id) {
      //  
      const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes/${id}`, {
        params: {
          attr_sel: this.activeName
        }
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.editForm = res.data
      this.editdialogVisible = true
    },
    editdiologclosed () {
      this.$refs.editFormref.resetFields()
    },
    // 
    editParams () {
      this.$refs.editFormref.validate(async valid => {
        if (!valid) {
          return this.$message.error('')
        }
        const { data: res } = await this.$http.put(`categories/${this.cateId}/attributes/${this.editForm.attr_id}`, {
          attr_name: this.editForm.attr_name,
          attr_sel: this.activeName
        })
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.editdialogVisible = false
        return this.getParamsData()
      })
    },
    // 
    async removeP (id) {
      const conres = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (conres !== 'confirm') {
        return this.$message.info('')
      }
      const { data: res } = await this.$http.delete(`categories/${this.cateId}/attributes/${id}`)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      return this.getParamsData()
    },
    // 
    async handleInputConfirm (row) {
      if (row.inputValue.trim().length === 0) {
        row.inputValue = ''
        row.inputVisible = false
        return false
      }
      //  
      row.attr_vals.push(row.inputValue.trim())
      row.inputValue = ''
      row.inputVisible = false
      this.saveAttrVals(row)
    },
    // 
    showInput (row) {
      row.inputVisible = true
      // $nextTick 
      //  
      //  
      this.$nextTick(() => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    // 
    handleColse (i, row) {
      row.attr_vals.splice(i, 1)
      this.saveAttrVals(row)
    },
    //  ajax
    async saveAttrVals (row) {
      //  
      const { data: res } = await this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`, {
        attr_name: row.attr_name,
        attr_sel: row.attr_sel,
        attr_vals: row.attr_vals.join('')
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      return this.$message.success('')
    }
  },
  created () {
    this.getCateList()
  }
}
</script>
<style scoped>
.el-cascader {
  margin-left: 20px;
}
.el-button {
  margin-bottom: 15px;
}
.el-tag {
  margin: 10px;
}
.input-new-tag {
  width: 120px;
}
</style>
