<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col :span="8">
          <el-input placeholder="">
            <el-button
              slot="append"
              icon="el-icon-search"
            ></el-button></el-input
        ></el-col>
      </el-row>
      <!--  -->
      <el-table :data="orderlist" border stripe>
        <el-table-column type="index"> </el-table-column>
        <el-table-column label="" prop="order_number">
        </el-table-column>
        <el-table-column label="" prop="order_price"> </el-table-column>
        <el-table-column label="" prop="pay_status">
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.pay_status === '1'"
              ></el-tag
            >
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="is_send"> </el-table-column>
        <el-table-column label="" prop="create_time">
          <template slot-scope="scope">
            <div>
              {{ scope.row.create_time | dateFormat }}
            </div>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showBox"
            ></el-button>
            <el-button
              type="success"
              icon="el-icon-location"
              size="mini"
              @click="showProgressBox"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 15, 50]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addressVisible"
      width="30%"
      @close="addClose"
    >
      <el-form
        :model="addressForm"
        :rules="addressRules"
        ref="addressref"
        label-width="100px"
      >
        <el-form-item label="/" prop="address1">
          <el-cascader :options="cityDate" v-model="addressForm.address1">
          </el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="addressForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addressVisible = false"> </el-button>
        <el-button type="primary" @click="addressVisible = false"
          > </el-button
        >
      </span>
    </el-dialog>
    <el-dialog title="" :visible.sync="dialogVisible" width="50%">
      <!-- timeline -->
      <el-timeline>
        <el-timeline-item
          v-for="(activity, index) in proInfo"
          :key="index"
          :timestamp="activity.time"
          :color="activity.color"
        >
          {{ activity.context }}
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>
<script>
import cityDate from './citydata'
export default {
  data () {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      total: 0,
      orderlist: [],
      addressVisible: false,
      addressForm: {
        address1: [],
        address2: ''
      },
      addressRules: {
        address1: [{ required: true, message: '', trigger: 'blur' }],
        address2: [{ required: true, message: '', trigger: 'blur' }]
      },
      cityDate: cityDate,
      dialogVisible: false,
      proInfo: [
        {
          time: '2018-05-10 09:39:00',
          ftime: '2018-05-10 09:39:00',
          context: ',,',
          location: '',
          color: 'red'
        },
        {
          time: '2018-05-10 08:23:00',
          ftime: '2018-05-10 08:23:00',
          context: '[]  95338',
          location: '',
          color: '#0bbd87'
        },
        {
          time: '2018-05-10 07:32:00',
          ftime: '2018-05-10 07:32:00',
          context: ' []',
          location: '',
          color: '#0bbd87'
        },
        {
          time: '2018-05-10 02:03:00',
          ftime: '2018-05-10 02:03:00',
          context: '[], []',
          location: '',
          color: '#0bbd87'
        },
        {
          time: '2018-05-09 23:05:00',
          ftime: '2018-05-09 23:05:00',
          context: ' []',
          location: '',
          color: '#0bbd87'
        },
        {
          time: '2018-05-09 21:21:00',
          ftime: '2018-05-09 21:21:00',
          context: '[], []',
          location: '',
          color: '#0bbd87'
        },
        {
          time: '2018-05-09 13:07:00',
          ftime: '2018-05-09 13:07:00',
          context: ' ',
          location: '',
          color: '#0bbd87'
        },
        {
          time: '2018-05-09 12:25:03',
          ftime: '2018-05-09 12:25:03',
          context: '',
          location: '',
          color: '#0bbd87'
        },
        {
          time: '2018-05-09 12:22:24',
          ftime: '2018-05-09 12:22:24',
          context: 'HLA',
          location: '',
          color: '#0bbd87'
        },
        {
          time: '2018-05-08 21:36:04',
          ftime: '2018-05-08 21:36:04',
          context: '',
          location: '',
          color: '#0bbd87'
        }
      ]
    }
  },
  methods: {
    async getOrderList () {
      const { data: res } = await this.$http.get('orders', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.total = res.data.total
      this.orderlist = res.data.goods
    },
    handleSizeChange (newSize) {
      this.queryInfo.pagesize = newSize
      this.getOrderList()
    },
    handleCurrentChange (newpage) {
      this.queryInfo.pagenum = newpage
      this.getOrderList()
    },
    // 
    showBox () {
      this.addressVisible = true
    },
    addClose () {
      this.$refs.addressref.resetFields()
    },
    async showProgressBox () {
      this.dialogVisible = true
    }
  },
  created () {
    this.getOrderList()
  }
}
</script>
<style scoped>
.el-cascader {
  width: 100%;
}
</style>
