<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row :gutter="20" class="first">
        <el-col :span="8">
          <el-form>
            <!--  -->
            <el-input
              placeholder=""
              v-model="queryInfo.query"
              class="input-with-select"
              clearable
              @clear="getGoodsList"
            >
              <el-button
                slot="append"
                icon="el-icon-search"
                @click="getGoodsList"
                native-type="submit"
              ></el-button>
            </el-input>
          </el-form>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="goAddPage"></el-button>
        </el-col>
      </el-row>
      <!-- table  -->
      <el-table :data="goodList" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="goods_name" label=""> </el-table-column>
        <el-table-column prop="goods_price" label="()" width="95px">
        </el-table-column>
        <el-table-column prop="goods_weight" label="" width="90px">
        </el-table-column>
        <el-table-column prop="add_time" label="" width="180px">
          <template slot-scope="scope">
            {{ scope.row.add_time | dateFormat }}
          </template></el-table-column
        >
        <el-table-column label="" width="130px">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="removeById(scope.row.goods_id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        background
      >
      </el-pagination>
    </el-card>
  </div>
</template>
<script>
export default {
  data () {
    return {
      // 
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      // 
      goodList: [],
      //  
      total: 0
    }
  },
  computed: {},
  methods: {
    // 
    async getGoodsList () {
      const { data: res } = await this.$http.get('goods', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.goodList = res.data.goods
      this.total = res.data.total
      // this.$message.success('')
    },
    handleSizeChange (newSize) {
      this.queryInfo.pagesize = newSize
      this.getGoodsList()
    },
    handleCurrentChange (newPage) {
      this.queryInfo.pagenum = newPage
      this.getGoodsList()
    },
    // 
    async removeById (id) {
      await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        // 
        const { data: res } = await this.$http.delete(`goods/${id}`)
        if (res.meta.status === 200) {
          this.$message({
            type: 'success',
            message: '!'
          })
          return this.getGoodsList()
        }
        return this.$message.error('')
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    // 
    goAddPage () {
      this.$router.push('/goods/add')
    }
  },
  created () {
    this.getGoodsList()
  }
}
</script>
<style scoped>
.first {
  margin-bottom: 15px;
}
</style>
