<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="showaddDialog"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <tree-table
        :data="catList"
        :columns="columns"
        :selection-type="false"
        :expand-type="false"
        show-row-hover
        show-index
        border
        class="treetable"
      >
        <!--  -->
        <template slot="isok" slot-scope="scope">
          <i
            class="el-icon-success"
            v-if="scope.row === false"
            style="color: red"
          ></i>
          <i class="el-icon-error" v-else style="color: lightgreen"></i>
        </template>
        <!--  -->
        <template slot-scope="scope" slot="order">
          <el-tag v-if="scope.row.cat_level === 0"></el-tag>
          <el-tag type="success" v-else-if="scope.row.cat_level === 1"
            ></el-tag
          >
          <el-tag type="warning" v-else></el-tag>
        </template>
        <!--  -->
        <template slot-scope="scope" slot="opt">
          <el-button type="primary" icon="el-icon-edit" size="mini"
            ></el-button
          >
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            @click="deleteCaleById(scope.row.cat_id)"
            ></el-button
          >
        </template>
      </tree-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[3, 5, 10, 20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      @close="addlogClose"
      title=""
      :visible.sync="addCatdialogVisible"
      width="30%"
    >
      <!--  -->
      <el-form
        :model="addCatForm"
        :rules="rulesRules"
        ref="ruleFormRef"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="cat_name">
          <el-input v-model="addCatForm.cat_name"></el-input> </el-form-item
        ><el-form-item label="">
          <el-cascader
            v-model="seletedKeys"
            :options="parentCateList"
            :props="castProps"
            @change="handleChange"
            clearable
          ></el-cascader></el-form-item
      ></el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCatdialogVisible = false"> </el-button>
        <el-button type="primary" @click="addcat"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    return {
      columns: [{
        label: '',
        prop: 'cat_name'
      }, {
        label: '',
        type: 'template',
        template: 'isok'
      }, {
        label: '',
        type: 'template',
        template: 'order'
      }, {
        label: '',
        type: 'template',
        template: 'opt'
      }],
      //  
      catList: [],
      // 
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      // 
      addCatdialogVisible: false,
      // 
      total: 0,
      // 
      addCatForm: {
        cat_name: '',
        // 
        cat_pid: 0,
        // 
        cat_level: 0
      },
      rulesRules: {
        cat_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      parentCateList: [],
      castProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        expandTrigger: 'hover'
      },
      // 
      seletedKeys: []
    }
  },
  methods: {
    // 
    async getCatList () {
      const { data: res } = await this.$http.get('categories', { params: this.queryInfo })
      if (res.meta.status !== 200) {
        this.$message.error('')
      }
      this.catList = res.data.result
      this.total = res.data.total
    },
    // pagesize
    handleSizeChange (newSize) {
      this.queryInfo.pagesize = newSize
      // 
      this.getCatList()
    },
    // pageNum
    handleCurrentChange (newNum) {
      this.queryInfo.pagenum = newNum
      this.getCatList()
    },
    // 
    showaddDialog () {
      this.getParentCatList()
      this.addCatdialogVisible = true
    },
    // 
    async getParentCatList () {
      const { data: res } = await this.$http.get('categories', {
        params: {
          type: 2
        }
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.parentCateList = res.data
    },
    handleChange () {
      //  
      if (this.seletedKeys.length > 0) {
        // id
        this.addCatForm.cat_pid =
          this.seletedKeys[this.seletedKeys.length - 1]
        // 
        this.addCatForm.cat_level = this.seletedKeys.length
      } else {
        // id
        this.addCatForm.cat_pid = 0
        this.addCatForm.cat_level = 0
      }
    },
    // 
    addcat () {
      this.$refs.ruleFormRef.validate(async valid => {
        if (!valid) {
          return this.$message.error('')
        }
        const { data: res } = await this.$http.post('categories', this.addCatForm)
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getCatList()
        this.addCatdialogVisible = false
      })
    },
    //   
    addlogClose () {
      this.$refs.ruleFormRef.resetFields()
      this.seletedKeys = []
      this.addCatForm.cat_pid = 0
      this.addCatForm.cat_level = 0
    },
    // id
    async deleteCaleById (id) {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        const { data: res } = await this.$http.delete('categories/' + id)
        if (res.meta.status === 200) {
          this.$message.success('')
          return this.getCatList()
        } else {
          return this.$message.error('')
        }
      }).catch(() => {
        this.$message.info('')
      })
    }

  },
  created () {
    this.getCatList()
  }
}
</script>
<style scoped>
.treetable {
  margin-top: 15px;
}
</style>
