<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="rolesDialogVisible = true"
            ></el-button
          >
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="roleList" border stripe row-key="id">
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row
              v-for="(item1, i1) in scope.row.children"
              :key="item1.id"
              :class="['bdbottom', i1 === 0 ? 'bdtop' : '', 'vcenter']"
            >
              <!--  -->
              <el-col :span="5">
                <el-tag>
                  {{ item1.authName }}
                  <i class="el-icon-caret-right"></i>
                </el-tag>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <!--  -->
                <el-row
                  v-for="item2 in item1.children"
                  :key="item2.id"
                  class="vcenter"
                >
                  <el-col :span="6">
                    <el-tag type="warning"
                      >{{ item2.authName }}<i class="el-icon-caret-right"></i>
                    </el-tag>
                  </el-col>
                  <!--  -->
                  <el-col :span="18">
                    <el-tag
                      type="success"
                      v-for="item3 in item2.children"
                      :key="item3.id"
                      >{{ item3.authName }}</el-tag
                    >
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column type="index" label="#"> </el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="ID" prop="id"></el-table-column>
        <el-table-column label="" width="300px">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="changeRoles(scope.row.id)"
              ></el-button
            >
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="delRoleById(scope.row.id)"
              ></el-button
            >
            <el-button
              type="warning"
              icon="el-icon-setting"
              size="mini"
              @click="showSetRight(scope.row)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--  -->
    <el-dialog
      @close="rloesDialogClosed"
      title=""
      :visible.sync="rolesDialogVisible"
      width="30%"
    >
      <el-form
        :model="addRoleObj"
        :rules="rolesRules"
        ref="addRoleObjRef"
        label-width="100px"
      >
        <el-form-item label="" prop="roleName">
          <el-input v-model="addRoleObj.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="addRoleObj.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="rolesDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addRoles"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="setRightVisible"
      width="30%"
      @close="setRightDioalogClosed"
    >
      <el-tree
        check-on-click-node
        :data="rightLists"
        :props="treeProps"
        show-checkbox
        node-key="id"
        default-expand-all
        :default-checked-keys="defKeys"
        ref="treeRef"
      ></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightVisible = false"> </el-button>
        <el-button type="primary" @click="allorRights"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    return {
      // 
      defKeys: [],
      // id
      roleId: '',
      // 
      roleList: [],
      rolesDialogVisible: false,
      setRightVisible: false,
      rightLists: [],
      addRoleObj: {
        roleId: '',
        roleName: '',
        roleDesc: ''
      },
      rolesRules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        roleDesc: []
      },
      // 
      treeProps: {
        label: 'authName',
        children: 'children'
      }
    }
  },
  methods: {
    // 
    async getRoleList () {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.roleList = res.data
    },
    // 
    rloesDialogClosed () {
      this.$refs.addRoleObjRef.resetFields()
    },
    // 
    addRoles () {
      this.$refs.addRoleObjRef.validate(async (valid) => {
        if (!valid) {
          return false
        }
        const { data: res } = await this.$http.post('roles', this.addRoleObj)
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.rolesDialogVisible = false
        this.getRoleList()
      })
    },
    // ID
    async delRoleById (id) {
      const { data: res } = await this.$http.delete('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getRoleList()
    },
    changeRoles (id) {
      this.$message.error('')
    },
    // 
    async showSetRight (role) {
      this.roleId = role.id
      // 
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // data
      this.rightLists = res.data
      // 
      this.getLeafKeys(role, this.defKeys)
      this.setRightVisible = true
    },
    // 
    getLeafKeys (node, arr) {
      //  
      if (!node.children) {
        return arr.push(node.id)
      }
      //  
      node.children.forEach(item => {
        this.getLeafKeys(item, arr)
      })
    },
    // 
    setRightDioalogClosed () {
      this.defKeys = []
    },
    // 
    async allorRights () {
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      const idStr = keys.join(',')
      const { data: res } = await this.$http.post(`roles/${this.roleId}/rights`, {
        rids: idStr
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getRoleList()
      this.setRightVisible = false
    }
  },
  // 
  created () {
    this.getRoleList()
  }
}
</script>
<style scoped>
.el-button {
  margin-bottom: 15px;
}
.el-tag {
  margin: 10px;
  font-size: 15px;
  border-bottom: 1px solid #ccc;
}
.bdtop {
  border-top: 1px solid #eee;
}
.bdbottom {
  border-bottom: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>
