<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="40">
        <el-col :span="9">
          <el-input
            placeholder=""
            v-model="queryInfo.query"
            clearable
            @clear="getUserList"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getUserList"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDiologVisible = true"
            ></el-button
          >
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="userList" style="width: 100%" border stripe>
        <el-table-column type="index"> </el-table-column>
        <el-table-column label="" width="180" prop="username">
        </el-table-column>
        <el-table-column label="" width="180" prop="email">
        </el-table-column>
        <el-table-column label="" width="180" prop="mobile">
        </el-table-column>
        <el-table-column label="" width="180" prop="role_name">
        </el-table-column>
        <el-table-column label="" width="180">
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.mg_state"
              @change="userStateChanged(scope.row)"
            >
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="" width="260">
          <template slot-scope="scope">
            <el-tooltip
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="showEditDialog(scope.row.id)"
              ></el-button>
            </el-tooltip>
            <el-tooltip
              effect="dark"
              :enterable="false"
              content=""
              placement="top"
            >
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="renmoveUserById(scope.row.id)"
              ></el-button>
            </el-tooltip>
            <el-tooltip
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="setRole(scope.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totel"
      >
      </el-pagination>
      <!--  -->
      <el-dialog
        @close="resgiter"
        title=""
        :visible.sync="addDiologVisible"
        width="20%"
      >
        <!--  -->
        <!--  -->
        <el-form
          :model="addForm"
          :rules="addFormRules"
          ref="addFormRef"
          label-width="70px"
        >
          <el-form-item label="" prop="username">
            <el-input v-model="addForm.username"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input v-model="addForm.password"></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model="addForm.email"></el-input>
          </el-form-item>
          <el-form-item label="" prop="mobile">
            <el-input v-model="addForm.mobile"></el-input></el-form-item
        ></el-form>
        <span slot="footer" class="dialog-footer">
          <!--  -->
          <el-button @click="addDiologVisible = false"> </el-button>
          <el-button type="primary" @click="addUser"> </el-button>
        </span>
      </el-dialog>
      <el-dialog
        title=""
        :visible.sync="editDialogVisible"
        width="30%"
        @close="editDialogClosed"
      >
        <el-form
          :model="editForm"
          :rules="addFormRules"
          ref="editFormRef"
          label-width="70px"
        >
          <el-form-item label="">
            <el-input v-model="editForm.username" disabled></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model="editForm.email"></el-input>
          </el-form-item>
          <el-form-item label="" prop="mobile">
            <el-input v-model="editForm.mobile"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="editUserInfo"> </el-button>
        </span>
      </el-dialog>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="setRolediolgo"
      width="30%"
      @close="seletdeRoleId = ''"
    >
      <p>{{ userInfo.username }}</p>
      <p>{{ userInfo.role_name }}</p>
      <p>
        <el-select v-model="seletdeRoleId" placeholder="">
          <el-option
            v-for="item in rolesList"
            :key="item.id"
            :label="item.roleName"
            :value="item.id"
          >
          </el-option>
        </el-select>
      </p>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRolediolgo = false"> </el-button>
        <el-button type="primary" @click="saveRoleInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    // 
    var checkEmail = (rule, value, callback) => {
      const regEmail = /^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/
      if (regEmail.test(value)) {
        return callback()
      }
      callback(new Error(''))
    }
    // 
    var checkMobile = (rule, value, callback) => {
      const regPhone = /^[1][3,4,5,7,8][0-9]{9}$/
      if (regPhone.test(value)) {
        return callback()
      }
      callback(new Error(''))
    }
    return {
      // id
      seletdeRoleId: '',
      // 
      rolesList: [],
      // 
      userInfo: '',
      // 
      setRolediolgo: false,
      // 
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },
      userList: [],
      totel: 0,
      // 
      editDialogVisible: false,
      addDiologVisible: false,
      addForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      // 
      editForm: {},
      addFormRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: '3-10', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: '5-12', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }]
      }
    }
  },
  methods: {
    async getUserList () {
      const { data: res } = await this.$http.get('users', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) { return this.$message.error('wrong get user info') }
      this.userList = res.data.users
      this.totel = res.data.total
    },
    handleSizeChange (newSize) {
      // console.log(newSize)
      this.queryInfo.pagesize = newSize
      this.getUserList()
    },
    handleCurrentChange (newPage) {
      // console.log(newPage)
      this.queryInfo.pagenum = newPage
      this.getUserList()
    },
    // 
    async userStateChanged (userinfo) {
      // console.log(userinfo.mg_state)
      const { data: res } = await this.$http.put(`users/${userinfo.id}/state/${userinfo.mg_state}`)
      if (res.meta.status !== 200) {
        userinfo.mg_state = !userinfo.mg_state
        return this.$message.error('')
      }
      this.$message.success('')
    },
    resgiter () {
      this.$refs.addFormRef.resetFields()
    },
    // 
    addUser () {
      this.$refs.addFormRef.validate(async (valid) => {
        // console.log(valid)
        if (!valid) { return false }
        const { data: res } = await this.$http.post('users', this.addForm)
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.$message.success('')
        // 
        this.addDiologVisible = false
        // 
        this.getUserList()
      })
    },
    // 
    async showEditDialog (id) {
      const { data: res } = await this.$http.get('users/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.editForm = res.data
      // console.log(res.data)
      this.editDialogVisible = true
      // console.log(id)
    },
    // 
    editDialogClosed () {
      this.$refs.editFormRef.resetFields()
    },
    //  
    editUserInfo () {
      this.$refs.editFormRef.validate(async (valid) => {
        // console.log(valid)
        if (!valid) {
          return false
        }
        // 
        const { data: res } = await this.$http.put('users/' + this.editForm.id, {
          email: this.editForm.email,
          mobile: this.editForm.mobile
        })
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        // 
        this.editDialogVisible = false
        // 
        this.getUserList()
        // 
        this.$message.success('')
      })
    },
    // id
    async renmoveUserById (id) {
      // 
      // console.log(id)
      await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        const { data: res } = await this.$http.delete('users/' + id)
        if (res.meta.status === 200) {
          this.$message({
            type: 'success',
            message: '!'
          })
          return this.getUserList()
        } else {
          return this.$message({
            type: 'info',
            message: ''
          })
        }
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    // 
    async setRole (userinfo) {
      this.userInfo = userinfo
      //  
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.rolesList = res.data
      this.setRolediolgo = true
    },
    async saveRoleInfo () {
      if (!this.seletdeRoleId) {
        return this.$message.error('')
      }
      const { data: res } = await this.$http.put(`users/${this.userInfo.id}/role`, {
        rid: this.seletdeRoleId
      })
      if (res.meta.status !== 200) {
        this.$message.error('')
      }
      this.$message.success('')
      this.getUserList()
      this.setRolediolgo = false
    }
  },
  created () {
    this.getUserList()
  }
}
</script>
<style scoped>
.el-table {
  margin-top: 20px;
}
</style>
