<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <div slot="header">
        <!--  -->
        <el-alert
          title=""
          type="info"
          center
          show-icon
          :closable="false"
          :size="mini"
        >
        </el-alert>
        <!--  -->
        <el-steps
          :space="200"
          :active="parseInt(activeIndex)"
          finish-status="success"
          :align-center="true"
        >
          <el-step title=""></el-step>
          <el-step title=""></el-step>
          <el-step title=""></el-step>
          <el-step title=""></el-step>
          <el-step title=""></el-step>
          <el-step title=""></el-step>
        </el-steps>
      </div>
      <div>
        <!--  -->
        <el-form
          :model="addForm"
          :rules="addFormrules"
          ref="addFormRef"
          label-width="100px"
          class="demo-ruleForm"
          label-position="top"
        >
          <!-- tab -->
          <el-tabs
            :tab-position="'left'"
            v-model="activeIndex"
            :before-leave="beforeLeave"
            @tab-click="tabClick"
          >
            <el-tab-pane label="" name="1">
              <el-form-item label="" prop="goods_name">
                <el-input v-model="addForm.goods_name"></el-input>
              </el-form-item>
              <el-form-item label="" prop="goods_price">
                <el-input
                  v-model="addForm.goods_price"
                  type="number"
                ></el-input>
              </el-form-item>
              <el-form-item label="" prop="goods_weight">
                <el-input
                  v-model="addForm.goods_weight"
                  type="number"
                ></el-input>
              </el-form-item>
              <el-form-item label="" prop="goods_number">
                <el-input
                  v-model="addForm.goods_number"
                  type="number"
                ></el-input>
              </el-form-item>
              <!--  -->
              <el-form-item label="" prop="">
                <el-cascader
                  v-model="addForm.goods_cat"
                  :options="catList"
                  :props="{
                    expandTrigger: 'hover',
                    label: 'cat_name',
                    value: 'cat_id',
                    children: 'children',
                  }"
                  @change="handleChange"
                ></el-cascader>
              </el-form-item>
            </el-tab-pane>
            <el-tab-pane label="" name="2">
              <!-- item -->
              <el-form-item
                :label="item.attr_name"
                v-for="item in manyTableDate"
                :key="item.attr_id"
              >
                <el-checkbox-group v-model="item.attr_vals">
                  <el-checkbox
                    :border="true"
                    :label="cb"
                    v-for="(cb, i) in item.attr_vals"
                    :key="i"
                  ></el-checkbox>
                </el-checkbox-group>
              </el-form-item>
            </el-tab-pane>
            <el-tab-pane label="" name="3">
              <el-form-item
                :label="item.attr_name"
                v-for="item in onlyTableData"
                :key="item.attr_id"
              >
                <el-input v-model="item.attr_vals"></el-input>
              </el-form-item>
            </el-tab-pane>
            <el-tab-pane label="" name="4">
              <!-- action api -->
              <el-upload
                action="http://127.0.0.1:8888/api/private/v1/upload"
                :headers="headerObj"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                list-type="picture"
                :on-success="handleSuccess"
              >
                <el-button size="small" type="primary"></el-button>
              </el-upload>
            </el-tab-pane>
            <el-tab-pane label="" name="5">
              <quill-editor v-model="addForm.goods_introduce"></quill-editor>
              <!--  -->
              <el-button type="primary" class="btnadd" @click="add"
                ></el-button
              >
            </el-tab-pane>
          </el-tabs>
        </el-form>
      </div>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible" width="50%">
      <img :src="preViewPath" class="previewimg" />
    </el-dialog>
  </div>
</template>
<script>
import _ from 'lodash'
export default {
  data () {
    return {
      dialogVisible: false,
      // 
      preViewPath: '',
      // headerObj
      headerObj: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // 
      onlyTableData: [],
      // 
      manyTableDate: [],
      // 
      activeIndex: '1',
      // 
      addForm: {
        goods_name: '',
        goods_price: null,
        goods_weight: null,
        goods_number: null,
        goods_cat: [],
        // 
        pics: [],
        // 
        goods_introduce: '',
        attrs: []
      },
      addFormrules: {
        goods_name: { required: true, message: '', trigger: 'blur' },
        goods_price: { required: true, message: '', trigger: 'blur' },
        goods_weight: { required: true, message: '', trigger: 'blur' },
        goods_numnber: { required: true, message: '', trigger: 'blur' }
      },
      catList: []
    }
  },
  computed: {
    cateId () {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2]
      }
      return null
    }
  },
  methods: {
    // 
    async getCateList () {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.catList = res.data
    },
    // 
    handleChange () {
      // 
      if (this.addForm.goods_cat.length !== 3) {
        this.addForm.goods_cat = []
      }
    },
    beforeLeave (activeName, oldActiveName) {
      if (oldActiveName === '1' && this.addForm.goods_cat.length !== 3) {
        this.$message.error('')
        return false
      }
    },
    async tabClick () {
      if (this.activeIndex === '2') {
        // 
        const { data: res } = await this.$http.get(`categories/${parseInt(this.cateId)}/attributes`, {
          params: { sel: 'many' }
        })
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        res.data.forEach(item => {
          item.attr_vals = item.attr_vals.length === 0 ? [] : item.attr_vals.split(' ')
        })
        this.manyTableDate = res.data
      } else if (this.activeIndex === '3') {
        const { data: res } = await this.$http.get(`categories/${parseInt(this.cateId)}/attributes`, {
          params: { sel: 'only' }
        })
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.onlyTableData = res.data
      }
    },
    // 
    handlePreview (file) {
      this.preViewPath = file.response.data.url
      this.dialogVisible = true
    },
    // 
    handleRemove (file) {
      const filePath = file.response.tmp_path
      const i = this.addForm.pics.findIndex(x => x.pic === filePath)
      this.addForm.pics.splice(i, 1)
    },
    // 
    handleSuccess (response) {
      // 1.
      const picInfo = { pic: response.data.tmp_path }
      // 2.  pics
      this.addForm.pics.push(picInfo)
    },
    add () {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) {
          return this.$message.error('')
        }
        // 
        //  
        // this.addForm.goods_cat = this.addForm.goods_cat.join(',')
        // lodash cloneDeep(obj)
        const from = _.cloneDeep(this.addForm)
        from.goods_cat = from.goods_cat.join(',')
        this.manyTableDate.forEach(item => {
          const newInfo = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals.join(' ')
          }
          this.addForm.attrs.push(newInfo)
        })
        this.onlyTableData.forEach(item => {
          const newInfo = { attr_id: item.attr_id, attr_value: item.attr_vals }
          this.addForm.attrs.push(newInfo)
        })
        from.attrs = this.addForm.attrs
        //  
        const { data: res } = await this.$http.post('goods', from)
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.$message.success('')
        return this.$router.push('/goods')
      })
    }
  },
  created () {
    this.getCateList()
  }
}
</script>
<style scoped>
.el-steps {
  margin-top: 24px;
}
.el-step__title {
  font-size: 10px;
}
.el-checkbok {
  margin: 0 5px 0 0 !important;
}
.previewimg {
  width: 100%;
}
.btnadd {
  margin-top: 15px;
}
</style>
