<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-table :data="rightList" style="width: 100%" border stripe>
        <el-table-column type="index"> </el-table-column>
        <el-table-column label="" prop="authName"> </el-table-column>
        <el-table-column label="" prop="path"> </el-table-column>
        <el-table-column label="" prop="level">
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.level === '0'"
              ></el-tag
            >
            <el-tag type="warning" v-else-if="scope.row.level === ''"
              ></el-tag
            >
            <el-tag type="danger" v-else-if="scope.row.level === '2'"
              ></el-tag
            >
            <el-tag type="info" v-else></el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>
<script>
export default {
  data () {
    return {
      // 
      rightList: []
    }
  },
  methods: {
    async getRightsList () {
      const { data: res } = await this.$http.get('rights/list')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.rightList = res.data
      // console.log(this.rightList)
    }
  },
  // 
  created () {
    this.getRightsList()
  }
}
</script>
